.section-history {
    box-sizing: border-box;
    padding: 10px;
    margin-top: 10%;
    width: 100%;
    min-height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: left;
    background-color: #8b8b8b;
    opacity: 95%;
    .content-history {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: left;
      background-color: #8b8b8b;
      opacity: 95%;
      margin: 10;
      padding: 0.5em;
      border: 10px solid #ccc;
      width: auto;
      word-wrap: break-word;
    }
    .body-history {
      outline: none;
      resize: none;
      margin-top: 0;
    }
    .history-delete-all {
      width: 100px;
      height: 30px;
      background-color: #ccc;
      margin: 10px;
      justify-content: center;
      align-items: center;
      transform: translateX(50%);
      &:hover {
        background-color: #aaa;
      }
      &:active {
        background-color: #999;
      }
    }
    #json-pretty {
      background: rgb(236, 232, 232);
      line-height: 1.3;
      white-space: pre-wrap; 
      white-space: -o-pre-wrap;
      word-wrap: break-word;
    }
  
    __json-key__ {
      color: rgb(70, 8, 145);
    }
  
    .__json-value__ {
      color: rgba(253, 176, 130, 1);
    }
  
    .__json-string__ {
      color: rgb(77, 159, 14);
    }
  
    .__json-boolean__ {
      color: rgb(156, 53, 115);
    }
  
    .__json-pretty-error__ {
      line-height: 1.3px;
      color: rgba(248, 248, 242, 1);
      background: #1e1e1e;
      overflow: auto;
    }
  }