section {
    box-sizing: border-box;
    padding: 10px;
    margin-top: 10%;
    width: 100%;
    min-height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: left;
    background-color: #8b8b8b;
    opacity: 95%;
  }
  section .content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: left;
    background-color: #8b8b8b;
    opacity: 95%;
    margin: 10;
    padding: 0.5em;
    border: 10px solid #ccc;
    width: 100%;
    word-wrap: break-word;
  }
  section .header {
    outline: none;
    resize: none;
    min-height: -webkit-fit-content;
    min-height: -moz-fit-content;
    min-height: fit-content;
    margin-bottom: 0;
  }
  section .body {
    outline: none;
    resize: none;
    margin-top: 0;
  }
  section #json-pretty {
    background: rgb(236, 232, 232);
    line-height: 1.3;
    white-space: pre-wrap; /* Since CSS 2.1 /
  white-space: -moz-pre-wrap;  / Mozilla, since 1999 /
  white-space: -pre-wrap;      / Opera 4-6 */
    white-space: -o-pre-wrap;
    word-wrap: break-word;
  }
  section __json-key__ {
    color: rgb(70, 8, 145);
  }
  section .__json-value__ {
    color: rgb(253, 176, 130);
  }
  section .__json-string__ {
    color: rgb(77, 159, 14);
  }
  section .__json-boolean__ {
    color: rgb(156, 53, 115);
  }
  section .__json-pretty-error__ {
    line-height: 1.3px;
    color: rgb(248, 248, 242);
    background: #1e1e1e;
    overflow: auto;
  }
  section .loader {
    color: #ffffff;
    font-size: 30px;
    text-indent: -9999em;
    overflow: hidden;
    width: 1em;
    height: 1em;
    border-radius: 50%;
    margin: 72px auto;
    position: relative;
    transform: translateZ(0);
    -webkit-animation: load6 1.7s infinite ease, round 1.7s infinite ease;
    animation: load6 1.7s infinite ease, round 1.7s infinite ease;
  }
